const _import = require('./_import_' + process.env.NODE_ENV)

export const routerMaps = [
  {
    path: '/',
    redirect: '/average'
  },
  {
    path: '/login',
    component: _import('login/index'),
    name: 'login',
    meta: {
      title: '登录'
    }
  },
  {
    path: '/average',
    component: _import('average/index'),
    name: 'average',
    meta: {
      title: '打分'
    }
  }
]



